
import { isNotInUni } from '../utils/utils';
import { chooseFile } from './choose-file';
import { chooseImage } from './choose-image';
import { chooseVideo } from './choose-video';



export function uniChooseFile({
  count,
  // sourceType,
  success,
  fail,
  type,
}) {

  if (isNotInUni()) {
    return chooseFile({
      count,
      sourceType: [],
      type,
      extension: ['*'],
      success,
    });
  }


  uni.chooseFile({
    count,
    type,
    success,
    fail,
  });
}


export function uniChooseImage({
  count,
  sourceType,
  sizeType,
  // type,
  success,
  fail,
}) {

  if (isNotInUni()) {
    return chooseImage({
      count,
      sourceType,
      extension: ['*'],
      success,
    });
  }


  uni.chooseImage({
    count,
    sourceType,
    sizeType,
    // type,
    success,
    fail,
  });
}


export function uniChooseVideo({
  sourceType,
  success,
  fail,

  compressed,
  maxDuration,
  camera,
}) {

  if (isNotInUni()) {
    return chooseVideo({
      sourceType,
      extension: ['*'],
      success,
    });
  }


  uni.chooseVideo({
    sourceType,
    // sizeType,
    // type,
    success,
    fail,

    compressed,
    maxDuration,
    camera,
  });
}
