export function chooseFile({
  accept,
  multiple,
  capture,
  compressed,
  maxDuration,
  sizeType,
  camera,
  maxCount,
}) {
  return new Promise((resolve, reject) => {
    switch (accept) {
      case 'image':
        uniChooseImage({
          count: multiple ? Math.min(maxCount, 9) : 1,
          sourceType: capture,
          sizeType,
          success: res => resolve(formatImage(res)),
          fail: reject,
        });
        break;














      case 'video':
        uniChooseVideo({
          sourceType: capture,
          compressed,
          maxDuration,
          camera,
          success: res => resolve(formatVideo(res)),
          fail: reject,
        });
        break;

        // 只有微信小程序才支持chooseMessageFile接口
      case 'file':









        // 需要hx2.9.9以上才支持uni.chooseFile
        uniChooseFile({
          count: multiple ? maxCount : 1,
          type: accept,
          success: res => resolve(formatFile(res)),
          fail: reject,
        });

        break;

      default:
        // 此为保底选项，在accept不为上面任意一项的时候选取全部文件









        // 需要hx2.9.9以上才支持uni.chooseFile
        uniChooseFile({
          count: multiple ? maxCount : 1,
          type: 'all',
          success: res => resolve(formatFile(res)),
          fail: reject,
        });

    }
  });
}
