import { chooseFile } from './utils';
import mixin from './mixin';
import props from './computed';
import { addUnit } from '../common/utils/add-unit';
import PressIconPlus from '../press-icon-plus/press-icon-plus.vue';
import PressLoadingPlus from '../press-loading-plus/press-loading-plus.vue';
import * as test from './test';
import { isNotInUni } from '../common/utils/utils';


export default {
  name: 'PressUploader',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  components: {
    PressIconPlus,
    PressLoadingPlus,
  },
  mixins: [mixin, props],
  emits: [
    'afterRead',
    'error',
    'oversize',
  ],
  data() {
    return {



      lists: [],
      isInCount: true,
      isNotInUni: isNotInUni(),
    };
  },
  watch: {
    // 监听文件列表的变化，重新整理内部数据
    fileList: {
      immediate: true,
      handler() {
        this.formatFileList();
      },
    },
  },
  methods: {
    addUnit,
    formatFileList() {
      const {
        fileList = [], maxCount,
      } = this;
      const lists = fileList.map(item => Object.assign(Object.assign({}, item), {
        // 如果item.url为本地选择的blob文件的话，无法判断其为video还是image，此处优先通过accept做判断处理
        isImage: this.accept === 'image' || test.image(item.url || item.thumb),
        isVideo: this.accept === 'video' || test.video(item.url || item.thumb),
        deletable: typeof(item.deletable) === 'boolean' ? item.deletable : this.deletable,
      }));
      this.lists = lists;
      this.isInCount = lists.length < maxCount;
    },
    chooseFile() {
      const {
        maxCount,
        multiple,
        lists,
        disabled,
      } = this;
      console.log('[chooseFile]');
      if (disabled) return;
      // 如果用户传入的是字符串，需要格式化成数组
      let capture;
      try {
        capture = Array.isArray(this.capture) ? this.capture : this.capture.split(',');
      } catch (e) {
        capture = [];
      }
      chooseFile(Object.assign({
        accept: this.accept,
        multiple: this.multiple,
        capture,
        compressed: this.compressed,
        maxDuration: this.maxDuration,
        sizeType: this.sizeType,
        camera: this.camera,
      }, {
        maxCount: maxCount - lists.length,
      }))
        .then((res) => {
          this.onBeforeRead(multiple ? res : res[0]);
        })
        .catch((error) => {
          this.$emit('error', error);
        });
    },
    // 文件读取之前
    onBeforeRead(file) {
      const {
        beforeRead,
        useBeforeRead,
      } = this;
      let res = true;
      // beforeRead是否为一个方法
      if (test.func(beforeRead)) {
        // 如果用户定义了此方法，则去执行此方法，并传入读取的文件回调
        res = beforeRead(file, this.getDetail());
      }
      if (useBeforeRead) {
        res = new Promise((resolve, reject) => {
          this.$emit(
            'beforeRead',
            Object.assign(
              Object.assign({
                file,
              }, this.getDetail()),
              {
                callback: (ok) => {
                  ok ? resolve() : reject();
                },
              },
            ),
          );
        });
      }
      if (!res) {
        return;
      }
      if (test.promise(res)) {
        res.then(data => this.onAfterRead(data || file));
      } else {
        this.onAfterRead(file);
      }
    },
    getDetail(index) {
      return {
        name: this.name,
        index: index == null ? this.fileList.length : index,
      };
    },
    onAfterRead(file) {
      const {
        maxSize,
        afterRead,
      } = this;
      const oversize = Array.isArray(file)
        ? file.some(item => item.size > maxSize)
        : file.size > maxSize;
      if (oversize) {
        this.$emit('oversize', Object.assign({
          file,
        }, this.getDetail()));
        return;
      }
      if (typeof afterRead === 'function') {
        afterRead(file, this.getDetail());
      }
      this.$emit('afterRead', Object.assign({
        file,
      }, this.getDetail()));
    },
    deleteItem(index) {
      this.$emit(
        'delete',
        Object.assign(Object.assign({}, this.getDetail(index)), {
          file: this.fileList[index],
        }),
      );
    },
    // 预览图片
    onPreviewImage(item) {
      if (!item.isImage || !this.previewFullImage) return;
      uni.previewImage({
        // 先filter找出为图片的item，再返回filter结果中的图片url
        urls: this.lists.filter(item => this.accept === 'image' || test.image(item.url || item.thumb)).map(item => item.url || item.thumb),
        current: item.url || item.thumb,
        fail() {
          // uni.$u.toast('预览图片失败');
        },
      });
    },
    onPreviewVideo(event) {
      if (!this.data.previewFullImage) return;
      const {
        index,
      } = event.currentTarget.dataset;
      const {
        lists,
      } = this.data;
      wx.previewMedia({
        sources: lists
          // .filter(item => test.video(item))
          .map(item => Object.assign(Object.assign({}, item), {
            type: 'video',
          })),
        current: index,
        fail() {
          // uni.$u.toast('预览视频失败');
        },
      });
    },
    onClickPreview(event) {
      const {
        index,
      } = event.currentTarget.dataset;
      const item = this.data.lists[index];
      this.$emit(
        'clickPreview',
        Object.assign(Object.assign({}, item), this.getDetail(index)),
      );
    },
  },
};
